.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    width: 1500px;
    height: 1500px;
    border-radius: 30px;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

p {
  font-size: larger;
  margin-bottom: 10px;
  color: rgb(255, 60, 80);
  font-weight: bolder;
}
h3 {
  background-color: #ffffff;
  margin: 10px;
  color: slateblue;
  padding: 10px;
  border-radius: 30px;
  font-family: "Courier New", Courier, monospace;
}
